import React, { Component } from 'react';
import {Mutation} from 'react-apollo';
import Form from './styles/Form';
import Link from 'next/link';
import formatMoney from '../lib/formatMoney';
import styled from 'styled-components';
import gql from 'graphql-tag';

const CREATE_ITEM_MUTAION = gql`
	mutation CREATE_ITEM_MUTAION(
		title : String!
		description : String!
		image : String
		largeImage : String
		price : Int!
	) {
		createItem(
			title : $title
			description : $description
			image : $image
			largeImage : $largeImage
			price : $price			
		){
			id
		}
	}
`;

export class CreateItem extends Component {
	state = {
		title : '',
		description: '',
		image : '',
		largeImage : '',
		price : 0
	}

	handleChange = (event) =>{
		const {name, type, value} = event.target;
		const val = type === 'number' ? parseFloat(value) : value;
		this.setState({
			[name] : val 
		});
	};

	render() {
		return (
			<Form onSubmit={event =>{
				event.preventDefault();

				}}>
				<fieldset>
					<label htmlFor="title">
						Title
						<input type="text" id="title" name="title" placeholder="Title" required value={this.state.title} onChange={this.handleChange} />
					</label>
					<label htmlFor="price">
						Price
						<input type="number" id="price" name="price" placeholder="price" required value={this.state.price} onChange={this.handleChange} />
					</label>
					<label htmlFor="description">
						Description
						<textarea id="description" name="description" placeholder="description" required value={this.state.description} onChange={this.handleChange} />
					</label>
					<button type="submit">Submit</button>
				</fieldset>
			</Form>
		);
	}
}

export {CREATE_ITEM_MUTAION};